/*headers*/
h1{

}

h2{
  font-size: 36px;
  color: #151515;
  line-height: 1;
  letter-spacing: 0;
  font-family: 'NHaasGroteskTXPro-Rg';
  margin-top: 24px;
  margin-left: 44px;
  position: relative;
}
h2 span{
  color: #dc0000;
  font-family: 'NHaasGroteskTXPro-Bd';
}
h2::before{
  content: "";
  width: 39px;
  height: 2px;
  background-color: #dc0000;
  position: absolute;
  top: 50%;
  left: -52px;
}

h3{
  color:#151515;
  font-size: 24px;
  font-family: 'NHaasGroteskTXPro-Bd';
}

h4{
	
}

h5{
	
}

h6{
	
}

/*end headers*/

/*other tags*/

#wrap{
  position: relative;
}
.content{
  padding-top: 136px;
  min-height: calc(100vh - 55px);
}
*{
  outline: 0;
  background-repeat: no-repeat;
}
body{
  background-color: #fff;
  font-family: 'NHaasGroteskTXPro-Rg';
}
p{
  font-size: 14px;
  line-height: 21px;
  color: #7b7b7b;
}
a:link,
a:visited{

}
a:hover,
a:active{
	text-decoration: none;
}
input[type=submit] {
  border-radius: 0;
}
/*end other tags*/

.clear{
  clear: both;
}

.standart_header{
  h2{
    font-size: 24px;
    line-height: 1.2;
    margin-top: 0px;
    position: relative;
    top: -7px;
    margin-bottom: 0;
  }
  p{
    color: #151515;
    font-size: 24px;
    line-height: 1.3;
    font-family: 'NHaasGroteskTXPro-Bd';
    margin-left: 44px;
    max-width: 850px;
  }
}
@media all and (max-width: 414px){
  h2{
    font-size: 24px;
  }
  .content{
    padding-top: 80px;
  }
}
@media (min-width: 1200px){
  .container {
    max-width: 1170px;
  }
}